.containar {
    padding: 2rem 0 5rem 0;
    background-color: rgba(26, 26, 25, 0.692);
  }
  footer {
    height: 25vh;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 auto;
    font-family: Fonts;
    color: white;
  }
  .row {
    display: flex;
    flex-wrap: wrap;
  }
  ul {
    list-style: none;
  }
  .footer-content {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    margin-right: 5rem;
    margin-left: 5rem;
  }
  .footer-content h4 {
    margin-bottom: 2rem;
    align-items: center;
    font-size: 20px;
    color: #f9f8f8;
    text-transform: capitalize;
    margin-bottom: 10px;
    font-weight: 500;
    position: relative;
    padding-top: 5px;
  }
  .footer-content h4::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: -10px;
    background-color: #e91e63;
    height: 2px;
    font-family: "Poppins", sans-serif;
    box-sizing: border-box;
    width: 50px;
  }
  .footer-content h3 {
    text-align: center;
    font-size: 25px;
    padding-bottom: 10px;
    padding-top: 20px;
  }
  .footer-content ul li:not(:last-child) {
    margin-bottom: 10px;
  }
  .footer-content ul li a {
    font-size: 18px;
    margin-right: 2rem;
    padding-right: 5rem;
    text-transform: capitalize;
    color: #fbf4f4;
    text-decoration: none;
    margin-bottom: 0.5rem;
    font-weight: 300;
    color: #bcb7b7;
    display: inline-block;
  }
  .footer-content ul li a:hover {
    color: #ffffff;
    padding-left: 5px;
    padding-right: 5px;
    transition: all 0.3s ease;
  }
  .footer-content .social-links a {
    display: inline-block;
    height: 40px;
    width: 40px;
    background-color: rgba(255, 255, 255, 0.2);
    margin: 0 10px 10px 0;
    text-align: center;
    line-height: 40px;
    border-radius: 50%;
    color: #ffffff;
  }
  h3 {
    font-size: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
    font-weight: bold;
    text-transform: capitalize;
    text-align: center;
  }
  .footer-bottom {
    text-align: center;
    padding-top: 10px;
  }
  .fa {
    padding: 20px;
    font-size: 30px;
    width: 50px;
    text-align: center;
    text-decoration: none;
  }
  
  /* Add a hover effect if you want */
  .fa:hover {
    opacity: 0.7;
  }
  
  /* Set a specific color for each brand */
  
  /* Facebook */
  .fa-facebook {
    background: #3B5998;
    color: white;
  }
  
  /* Twitter */
  .fa-twitter {
    background: #55ACEE;
    color: white;
  }

@media screen and (max-width: 768px) {
  footer {
    height: 30vh;
  }
  @media screen and (max-width: 896px) {
    footer {
      height: 100vh;
  }
}
  @media screen and (max-width: 700px) {
    footer {
      padding-bottom: 2vh;
    }
  }
}